@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&family=Urbanist:wght@100;200;300;400;500;600;700;800;900&display=swap');
// Override Default Styles
#wpcontent {
    padding: 0 !important;
}

:root {
    --zolo-dashboard-brand-color: #3f8efc;
    --zolo-badge-pro-color: #ffa826;
    --zolo-badge-pro-h-color: rgba(255, 168, 38, 0.2);
    --zolo-badge-upcoming-color: #009375;
    --zolo-badge-upcoming-h-color: rgba(0, 255, 203, 0.149);
    --zolo-dash-brand-hover-color: #1475fe;
}

.zolo-dash-flex-center {
    display: flex;
    align-items: center;
}

.zolo-settings-head-content {
    gap: 50px;
    .zolo-settings-type-badge {
        box-shadow: none;
        border: 1px solid #ededed;
        border-radius: 6px;
        display: none;
        .zolo-settings-type-btn {
            border: 0;
            padding: 8px 15px;
            cursor: pointer;
            background: transparent;
            font-weight: 500;
            &.active {
                color: var(--zolo-dashboard-brand-color);
            }
            &:first-child {
                border-right: 1px solid #ededed;
            }
        }
    }
}

.zolo-header {
    .zolo-search-bar {
        display: none;
    }
    .zolo-current-version {
        display: none;
    }
}

.toplevel_page_zoloblocks {
    background-color: #f5f5f5;
    #wpfooter {
        display: none;
    }

    &.folded {
        .zolo-footer {
            padding-left: 60px;
        }
    }

    // site visibility create a new page modal css here
    .zolo-maintenance-modal {
        &.zolo-sv-create-new-page-modal {
            max-width: 100%;
            height: 100%;
            max-height: 100%;
            --zolo-modal-header-height: 70px;
            .components-modal__content {
                padding: 0;
            }
            .zolo-modal-header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                background: #f0f0f0;
                padding: 20px;
                z-index: 99;
                position: relative;
                height: var(--zolo-modal-header-height);
                .zolo-modal-head-title {
                    margin: 0;
                    font-size: 20px;
                    font-weight: 700;
                }
                .zolo-modal-close {
                    cursor: pointer;
                    padding: 10px;
                    color: #000000;
                    transition: color 0.3s ease;
                    svg {
                        width: 18px;
                        height: 18px;
                        display: block;
                    }
                    &:hover {
                        color: #f00;
                    }
                }
            }
            .zolo-modal-iframe {
                height: calc(100vh - var(--zolo-modal-header-height));
                width: 100%;
                border: 0;
            }
        }
    }
}

.zolo-dashboard-container {
    display: flow-root;
    /* 1 */
    box-sizing: content-box;
    max-width: 1400px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}

.header-flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.zolo-logo img {
    width: clamp(8rem, 14vw, 11.75rem);
    vertical-align: middle;
}

.zolo-dashboard-sidebar {
    .zolo-tabs {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    .zolo-tab {
        border: none;
        background: none;
        padding: 12px 20px;
        font-size: 16px;
        cursor: pointer;
        line-height: 1;
        font-weight: 500;
        border-radius: 6px;
        display: flex;
        align-items: center;
        gap: 10px;
        &.zolo-tab-active {
            color: var(--zolo-dashboard-brand-color);
            background-color: #e4ecff;
        }

        .zolo-side-tab-icon {
            svg {
                display: block;
                width: 26px;
                height: 26px;
                path {
                    stroke: currentColor;
                }
            }
        }
    }
}

.zolo-extra-info {
    display: flex;
    align-items: center;
    gap: clamp(0.625rem, 1.5vw, 0.938rem);
}

.zolo-current-version.single-info {
    color: #ffffff;
    font-size: 14px;
    padding: 12px 20px;
    border-radius: 30px;
    font-weight: 700;
    background-color: var(--zolo-dashboard-brand-color);
}

.zolo-changes-logs.single-info .logos-panel-btn {
    border: 1px solid #ababab;
    background: none;
    width: clamp(1.875rem, 4vw, 2.5rem);
    height: clamp(1.875rem, 4vw, 2.5rem);
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.zolo-changes-logs.single-info .logos-panel-btn svg {
    width: 14px;
    height: 14px;
    display: block;
}

.zolo-changes-logs.single-info {
    display: flex;
    align-items: center;
    justify-content: center;
}

.zolo-changelog-plugin-name {
    padding: 20px 30px;
    background-color: #eef0f2;
}

.zolo-changelog-plugin-name h3 {
    margin: 0;
    font-size: 18px;
}

.logos-panel {
    position: fixed;
    top: 0;
    right: 0;
    background: #fff;
    height: 100%;
    width: 360px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
    z-index: 999;
    overflow-y: scroll;
}

.logos-panel-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 30px 10px;
    border-bottom: 1px solid #dddddd;
    position: sticky;
    top: 0;
    background-color: #fff;
}

.logos-panel-close-btn {
    background: none;
    border: none;
    cursor: pointer;
}

.single-log {
    padding: 20px 30px;

    + .single-log {
        border-top: 1px solid #dddddd;
    }

    .version {
        margin: 0 0 5px;
        line-height: 1;
        font-size: 16px;
        font-weight: 700;
    }

    .date {
        font-size: 12px;
        margin: 0;
        color: #999;
    }

    h5.changes-title {
        font-size: 15px;
        margin: 0 0 9px;
        line-height: 1;
        font-weight: 700;
    }

    .log-header {
        margin-bottom: 15px;
    }

    .changes ul {
        margin: 0;
        padding: 0;
        padding-left: 20px;
        list-style-type: disc;
        li {
            margin-bottom: 9px;
            color: #696c76;
        }
    }

    .changes + .changes {
        margin-top: 15px;
    }
}
.logos-panel-body {
    height: 100%;
}
.zolo-header-main-wrap {
    // body
    .zolo-body {
        margin: clamp(20px, 2vw, 30px);
    }
}

.zoloblocks-grid {
    height: 100%;
    width: 100%;
}

.zoloblocks-grid .zoloblocks-inner-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 15px;
    // flex-wrap: wrap;
}

.zolo-single-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    padding: clamp(14px, 1.5vw, 20px);
    box-sizing: border-box;
    border-radius: 10px;
    transition: box-shadow 0.3s;
    box-shadow: 0 0 0 1px #e7eaf5 inset;
    position: relative;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: 340px;
    &.active:hover {
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
    }
    .block-badge-toggle-wrap {
        display: flex;
    }
    &.ispro {
        .block-badge-toggle-wrap {
            gap: 15px;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-end;
        }
    }

    .block-pro {
        background: var(--zolo-badge-pro-h-color);
        color: var(--zolo-badge-pro-color);
        padding: 7px;
        line-height: 1;
        font-size: 10px;
        text-transform: uppercase;
        font-weight: 700;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        border-radius: 6px;
        margin-top: -15px;
    }

    .block-pro-badge-icon {
        svg {
            display: block;
            width: 13px;
            height: 13px;
            path {
                stroke: currentColor;
            }
        }
    }

    &.upcoming {
        background-color: transparent;
        pointer-events: none;
        &.ispro {
            .block-pro {
                display: none;
            }
        }
    }
}

.zolo-badge-upcoming {
    position: absolute;
    top: 0;
    right: 0;
    margin: 25px;
    padding: 15px;
    font-size: 12px;
    line-height: 1;
    text-transform: uppercase;
    font-weight: 700;
    border-radius: 6px;
    color: var(--zolo-badge-upcoming-color);
    background: var(--zolo-badge-upcoming-h-color);
}

.zolo-single-setting.upcoming,
.zolo-single-block.upcoming {
    box-shadow: 0 0 0 1px #e7eaf5 inset !important;
    background-color: rgba(255, 255, 255, 0.459) !important;
    pointer-events: none;
}

.zolo-single-block {
    &.upcoming {
        .zolo-badge-upcoming {
            margin: 15px;
            padding: 10px;
            font-size: 10px;
        }
    }
}

.zolo-settings-option-item.upcoming {
    position: relative;
    .zolo-badge-upcoming {
        margin-top: 22px;
        font-size: 12px;
        padding: 16px 20px;
    }
}

.zolo-single-block.active,
.filter-btn,
input.zoloblocks-search,
.blocks-filter-buttons,
.zolo-no-block-found,
.zolo-settings-tab-button,
.zolo-settings-option-item,
.zolo-settings-type-badge {
    background-color: #fff;
    --tw-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05);
    --tw-shadow-colored: 0 1px 2px 0 var(--tw-shadow-color);
    --tw-ring-offset-shadow: 0 0 #0000;
    --tw-ring-shadow: 0 0 #0000;
    box-shadow: var(--tw-ring-offset-shadow, 0 0 rgba(0, 0, 0, 0)), var(--tw-ring-shadow, 0 0 rgba(0, 0, 0, 0)), var(--tw-shadow);
}

.block-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: clamp(10px, 1vw, 13px);
}

.block-external-link {
    display: flex;
    align-items: center;
    gap: 10px;
}

.block-icon {
    background-color: #e2e8f0;
    padding: clamp(16px, 1.5vw, 18px);
    border-radius: 6px;
}

.block-icon svg {
    display: block;
    rect,
    g {
        width: 30px;
        height: 30px;
    }
}

.zolo-single-block {
    .block-icon {
        svg {
            path {
                fill: var(--zolo-dashboard-brand-color);
            }
        }
    }
}
.zolo-single-block.active {
    .block-icon {
        background-color: #e4ecff;
        svg {
            path {
                fill: var(--zolo-dashboard-brand-color);
            }
        }
    }
}

.block-title {
    margin: 0;
    padding: 0;
    font-size: clamp(16px, 1.5vw, 18px);
    cursor: pointer;
    flex: 1;
    font-weight: 600;
}

.block-switcher .components-base-control {
    margin: 0;
}

.zolo-settings-actions {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.zolo-settings-title {
    margin: 0;
    font-size: 20px;
    display: none;
}
.zoloblocks-actions-btn {
    display: none;
    align-items: center;
    gap: 10px;
    width: 100%;
    justify-content: flex-end;
    button {
        justify-content: center;
        border: 1px solid #e7eaf5;
    }

    .zolo-activated-btn.zolo-save-changes {
        background: #3f8efc;
        color: #fff;
        &:focus {
            color: #fff !important;
        }
    }
}

.zolo-category-wrap {
    display: flex;
    align-items: center;
    gap: 8px;
    .category-icon {
        svg {
            width: 18px;
            height: 18px;
            display: block;
            path {
                fill: currentColor;
            }
        }
    }
}

.filter-btn,
.zolo-activated-btn,
.zolo-deactivated-btn {
    border: 0;
    padding: clamp(0.625rem, 1vw, 0.75rem) clamp(0.875rem, 2vw, 1.25rem);
    font-size: clamp(0.75rem, 1vw, 0.875rem);
    font-weight: 500;
    border-radius: 6px;
    cursor: pointer;
    transition: 0.3s;
    display: flex;
    align-items: center;
    gap: clamp(0.5rem, 1vw, 0.625rem);
    background-color: #fff;
}

.zolo-activated-btn,
.zolo-deactivated-btn {
    &:focus {
        color: var(--zolo-dashboard-brand-color) !important;
        box-shadow: 0 0 0 2px var(--zolo-dashboard-brand-color);
    }
}

.blocks-filter-buttons {
    display: none;
    box-shadow: none;
    padding: 15px;
    border-radius: 10px;
    flex-wrap: wrap;
    gap: 10px;
    .filter-btn {
        box-shadow: none;
        padding: 0;
        color: #3a3c40;
        font-weight: 500;
        &.active {
            color: var(--zolo-dashboard-brand-color);
        }
        .category-icon {
            display: none;
        }
    }
}

.search-field {
    position: relative;
    .zolo-search-icon {
        z-index: 11;
        width: 16px;
        height: 16px;
        display: block;
    }
}

input.zoloblocks-search {
    padding: 10px 10px 10px 38px;
    position: relative;
    line-height: 1;
    border: 1px solid #ededed;
    border-radius: 6px;
    outline: none;
    box-sizing: border-box;
    box-shadow: none;
    width: 300px;
    &:focus {
        outline: none !important;
        box-shadow: 0 0 0 1px var(--zolo-dashboard-brand-color);
    }
}
.zolo-search-icon {
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    background: 0;
    border: 0;
    padding: 0;
    z-index: 99;
    cursor: pointer;
}

.clear-search {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    position: absolute;
    top: 50%;
    right: 15px;
    color: #9e9bb2;
    transform: translateY(-50%);
    &:hover {
        color: #35343d;
    }
}

.total-blocks {
    color: #35343d;
    background-color: rgba(212, 219, 249, 0.99);
    display: block;
    width: clamp(1.125rem, 1.5vw, 1.375rem);
    height: clamp(1.125rem, 1.5vw, 1.375rem);
    line-height: clamp(1.125rem, 1.5vw, 1.375rem);
    border-radius: 50%;
    font-size: clamp(0.5rem, 0.8vw, 0.625rem);
    font-weight: 700;
}
.block-info a {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: clamp(11px, 1vw, 13px);
    gap: 5px;
    text-decoration: none;
    color: #919aa6;
    transition: color 0.3s ease;
    &:hover {
        color: var(--zolo-dashboard-brand-color);
    }
}

.block-info {
    svg {
        display: block;
        width: 16px;
        height: 16px;
    }
}

.block-switcher,
.zolo-settings-option-item {
    .components-h-stack {
        gap: 0;
    }
    .components-form-toggle {
        height: 30px;
    }
    .components-form-toggle .components-form-toggle__thumb {
        height: 22px;
        left: 3px;
        top: 3px;
        width: 22px;
    }
    .components-form-toggle .components-form-toggle__track {
        border-radius: 14px;
        height: 28px;
        width: 55px;
    }
    .components-form-toggle.is-checked .components-form-toggle__thumb {
        transform: translateX(27px);
    }
    .components-form-toggle.is-checked .components-form-toggle__track {
        background-color: var(--zolo-dashboard-brand-color);
    }
}

.zolo-single-block,
.zolo-settings-option-item {
    &.ispro {
        .components-form-toggle.is-checked .components-form-toggle__track {
            background-color: var(--zolo-badge-pro-color);
        }
    }
    // .components-form-toggle.is-checked .components-form-toggle__track {
    //     background-color: var(--zolo-badge-pro-color);
    // }

    .components-form-toggle .components-form-toggle__track {
        background-color: #e2e8f0;
        border: 0;
    }
    .components-form-toggle .components-form-toggle__thumb {
        background-color: #fff;
        border: 5px solid #fff;
    }
    .components-form-toggle .components-form-toggle__input:focus + .components-form-toggle__track {
        box-shadow: none;
    }
    &.active {
        .components-form-toggle .components-form-toggle__input:focus + .components-form-toggle__track {
            box-shadow: none;
        }
    }
}
// header css

.zolo-tabs-dropbtn {
    padding: 12px;
    border-radius: 50%;
    background-color: #ffffff;
    cursor: pointer;
    transition: 0.3s ease;
    border: 1px solid #ddd;
    &:hover {
        background-color: #ccd0ed;
    }
}

.zolo-tabs-dropbtn svg {
    width: 14px;
    height: 14px;
    display: block;
}

.zolo-tabs-dropdown {
    position: relative;
    display: inline-block;
    padding: 0;
    z-index: 99;
}
.zolo-header-right {
    display: flex;
    align-items: center;
    gap: 15px;
    flex-direction: row-reverse;
}

.zolo-tabs-dropdown-content {
    display: none;
    position: absolute;
    background-color: #fff;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 111;
    right: 10px;
}

.zolo-tabs-dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.zolo-tabs-dropdown-content a:hover {
    background-color: #ddd;
}

.zolo-tabs-dropdown:hover .zolo-tabs-dropdown-content {
    display: block;
}

.zolo-tabs-dropdown:hover .dropbtn {
    background-color: #3e8e41;
}

.zolo-tabs-dropdown .zolo-tab {
    height: auto;
    width: 100%;
    padding: 10px 20px;
    font-size: 14px;
    border-width: 1px;
    border-color: #e6e6e6;
    text-align: left;
}
.zolo-tabs-dropdown .zolo-tab.zolo-tab-active {
    border-width: 2px;
    border-color: var(--zolo-dashboard-brand-color);
}

.zolo-single-setting {
    padding: clamp(1.875rem, 5vw, 3.125rem) !important;
}

.help-docs {
    top: clamp(1.875rem, 5vw, 3.125rem) !important;
    right: clamp(1.875rem, 5vw, 3.125rem) !important;
}

.settings-grid {
    gap: 30px !important;
    grid-template-columns: repeat(1, 1fr) !important;
}

.zoloblocks-list-tab {
    display: flex;
    gap: 15px;
    flex-direction: column;
}

@media (min-width: 768px) {
    .settings-grid {
        grid-template-columns: repeat(2, 1fr) !important;
    }
    .zolo-settings-actions {
        align-items: center;
        justify-content: space-between;
        flex-direction: row;
    }
}
@media (min-width: 1024px) {
    .total-blocks {
        background-color: rgba(239, 242, 255, 0.99);
    }
    .zolo-settings-actions {
        gap: 50px;
    }
    .zolo-tabs-dropdown {
        display: none;
        padding: 0;
    }
}

@media (min-width: 1440px) {
    .zolo-filter-header {
        display: block;
    }
    .zolo-settings-actions {
        gap: 100px;
    }
}

@media (min-width: 1640px) {
    .blocks-filter-buttons {
        gap: 0;
        .filter-btn {
            padding: 10px 15px;
            &.active {
                background-color: #e4ecff;
            }
            .category-icon {
                display: block;
            }
        }
    }
}

.zolo-no-block-found {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    align-items: center;
    border-radius: 10px;
    padding: 50px;
    img {
        width: 350px;
        margin-bottom: 20px;
    }
    .zolo-wrong-title {
        margin: 0 0 20px;
        font-size: 26px;
        text-transform: capitalize;
        font-weight: 700;
        color: #3d3d4b;
    }
    .zolo-wrong-text {
        margin: 0 0 25px;
        font-size: 16px;
        color: #727278;
        max-width: 500px;
        text-align: center;
    }
    .zolo-welcome-page-btn {
        padding: 14px 18px;
        border-radius: 30px;
        svg {
            width: 16px;
            height: 16px;
            display: block;
        }
    }
}

.zolo-footer-text {
    font-style: normal !important;
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    margin-bottom: 30px;
    a {
        color: var(--zolo-dashboard-brand-color);
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

// notice
.data-update-notice {
    position: fixed;
    top: 150px;
    right: 20px;
    color: #2b943c;
    background: #ffffff;
    border: 1px solid #e5e5e5;
    backdrop-filter: blur(15px);
    border-radius: 5px;
    padding: 15px 20px;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 8px;
    z-index: 1;
    visibility: hidden;
    transform: translateX(100%);
    transition: 0.3s ease;
    svg {
        fill: currentColor;
        width: 20px;
        height: 20px;
    }
    &.show {
        visibility: visible;
        transform: translateX(0);
    }
}

// new dashboard css here

.zolo-dashboard-wrapper {
    display: flex;
    box-sizing: border-box;
    position: relative;
    --zolo-dashboard-sidebar-width: 280px;
    max-width: 1720px;

    .button-primary {
        line-height: 1.5;
    }

    .zolo-dashboard-sidebar {
        width: var(--zolo-dashboard-sidebar-width);
        box-sizing: border-box;
        background-color: #fff;
        min-height: 100vh;
        padding: 30px;
        padding-right: 24px;
        position: fixed;
        border-right: 1px solid #efeef1;
        display: none;
    }

    .zolo-dash-sidebar-info {
        height: 85vh;
        overflow-x: hidden;
        overflow-y: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        mask-image: linear-gradient(#000 92%, transparent);
        margin-top: 30px;
        padding-right: 6px;
        /* width */
        &::-webkit-scrollbar {
            width: 3px;
            border-radius: 6px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            background: #f1f1f1;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #cccccc;
        }
    }

    .zolo-header-main-wrap {
        // margin-left: var(--zolo-dashboard-sidebar-width);
        width: 100%;
        box-sizing: border-box;
    }

    .zolo-dash-side-primum {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        border: 2px solid #fff0cb;
        background-color: #fffcf3;
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 30px;
    }
    .zolo-dash-sidebar-bottom-info-icon {
        border: 2px solid #ffa826;
        padding: 26px;
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        position: relative;
    }

    .zolo-dash-sidebar-bottom-info-icon svg {
        width: 30px;
        height: 30px;
        display: block;
    }

    .zolo-sidebar-icon-badge {
        position: absolute;
        top: -5px;
        right: -5px;
        background-color: var(--zolo-badge-pro-color);
        border-radius: 50%;
        padding: 6px;
    }

    .zolo-sidebar-icon-badge svg {
        display: block;
        width: 16px;
        height: 16px;
    }

    .zolo-dash-sidebar-bottom-info-text {
        font-size: 15px;
        color: #42454c;
        margin: 20px 0;
    }

    .zolo-dash-sidebar-bottom-info-link {
        color: #ffffff;
        text-decoration: none;
        font-weight: 600;
        font-size: 14px;
        padding: 12px 20px;
        border-radius: 30px;
        transition: 0.3s;
        background-color: var(--zolo-dashboard-brand-color);

        &:hover {
            background-color: var(--zolo-dash-brand-hover-color);
        }
    }

    .zolo-header {
        padding: clamp(20px, 2vw, 30px);
        padding-bottom: 0;
    }

    .zolo-header-left {
        display: flex;
        align-items: center;
        gap: 20px;
        .zolo-page-header-title {
            margin: 0;
            font-size: 22px;
            color: #3e3e44;
        }
        .zolo-search-bar {
            position: relative;
        }
    }
}

// zolo pro modal css start
.zolo-pro-modal {
    min-width: 450px;
    border-radius: 6px;
    box-shadow: none;

    .components-modal__content {
        padding: 40px;
        margin: 0;
        text-align: center;
    }
    .components-modal__header {
        padding: 0;
        margin: 0;
        height: auto;
        margin: 10px;
        width: auto;
        right: 0;
        left: unset;
        .components-button {
            padding: 0;
            min-width: none;
            margin: 20px;
            left: 0;
            position: unset !important;
            svg {
                color: #a3a7b1;
            }
            &:hover {
                svg {
                    color: #f00;
                }
            }
        }
    }
    .zolo-pro-modal-desc {
        margin: 10px 0 30px;
        font-size: 18px;
        color: #4e4e55;
        max-width: 430px;
    }

    .zolo-pro-modal-icon {
        width: 100px;
        height: 100px;
    }

    .zolo-pro-modal-btn {
        text-decoration: none;
        background-color: #ffe9e9;
        padding: 12px 20px;
        display: inline-block;
        color: #f00;
        font-weight: 600;
        font-size: 13px;
        border-radius: 6px;
        transition: 0.3s;
        display: inline-flex;
        align-items: center;
        gap: 5px;
        svg {
            width: 20px;
            height: 20px;
            display: block;
        }
        &:focus {
            outline: 2px solid rgba(255, 156, 156, 0.98) !important;
        }
        &:hover {
            background-color: #f00;
            color: #fff;
        }
    }
}

@media (min-width: 768px) {
    .zolo-header {
        .zolo-search-bar {
            display: block;
        }
        .zolo-current-version {
            display: block;
        }
    }
    .zoloblocks-actions-btn {
        display: flex;
    }
    .zoloblocks-grid .zoloblocks-inner-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1024px) {
    .zolo-dashboard-wrapper {
        .zolo-dashboard-sidebar {
            display: block;
        }
        .zolo-header-main-wrap {
            margin-left: var(--zolo-dashboard-sidebar-width);
        }
    }
    .blocks-filter-buttons,
    .zolo-settings-type-badge {
        display: flex;
    }
    .zolo-settings-title {
        display: block;
    }
    input.zoloblocks-search {
        padding: 13px 13px 13px 38px;
    }
}

@media (min-width: 1640px) {
    .zoloblocks-grid .zoloblocks-inner-grid {
        grid-template-columns: repeat(3, 1fr);
    }
}
